import NavbarLink from './NavbarLink';
import { scb_blue as logo } from '../../assets/images';
import './Navbar.styles.scss';
import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { useLocation } from 'react-router-dom';

const mock = [
    { text: "", to: "/news" },
    { text: " ", to: "/aboutus" },
    { text: "", to: "/labs" },
    { text: "", to: "/projects" },
    { text: "", to: "/contact" },
    { text: "", to: "https://ictis.sfedu.ru", newTab: true },
];

function Navbar({ links = mock }) {
    const [isOpen, setIsOpen] = useState(false);
    const [isTransparent, setIsTransparent] = useState(true); // Navbar  
    const [isHidden, setIsHidden] = useState(false);
    const [lastScrollTop, setLastScrollTop] = useState(0);
    const [scrolledOnce, setScrolledOnce] = useState(false); //    
    const THRESHOLD = 400; //    Navbar   
    const location = useLocation(); //    

    const toggleMenu = () => setIsOpen(!isOpen);
    const handleLinkClick = () => setIsOpen(false);

    //      Navbar
    useEffect(() => {
        if (location.pathname === '/') {
            setIsTransparent(true);
            window.scrollTo(0, 0);
        } else {
            setIsTransparent(false);
        }
        setScrolledOnce(false);
    }, [location]);

    useEffect(() => {
        const handleScroll = () => {
            const currentScrollTop = window.pageYOffset;
            if (!isOpen) {
            //   Navbar   
            if (currentScrollTop <= THRESHOLD) {
                setIsHidden(false); // Navbar ,   
            } else {
                if (currentScrollTop > lastScrollTop) {
                    setIsHidden(true); //  Navbar   
                } else {
                    setIsHidden(false); //  Navbar   
                }
            }
        }
            //        
            if (location.pathname === "/" && !scrolledOnce && currentScrollTop > 50) {
                setIsTransparent(false);
                setScrolledOnce(true);
            }

            setLastScrollTop(currentScrollTop);
        };

        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, [lastScrollTop, scrolledOnce, location.pathname, isOpen]);

    //       (   "")
    useEffect(() => {
        const handlePopState = () => {
            if (location.pathname === '/') {
                setIsTransparent(true);
                //       
                window.scrollTo(0, 0);
            } else {
                setIsTransparent(false);
            }
        };

        window.addEventListener('popstate', handlePopState);
        return () => window.removeEventListener('popstate', handlePopState);
    }, [location]);

    return (
        <div id="navbar" className={`${isTransparent ? "transparent" : ""} ${isHidden ? "hidden" : ""}`}>
            <NavbarLink className="logo-container" link={{
                to: "/",
                onClick: () => {
                    window.scrollTo(0, 0);
                }
            }}>
                <img src={logo} className="logo" alt="SCB logo" />
            </NavbarLink>
            <div className="burger" onClick={toggleMenu}>
                <span className={`line ${isOpen ? 'open' : ''}`}></span>
                <span className={`line ${isOpen ? 'open' : ''}`}></span>
                <span className={`line ${isOpen ? 'open' : ''}`}></span>
            </div>
            <div className={`bar ${isOpen ? 'open' : ''}`}>
                {links.map((link, index) => (
                    <NavbarLink link={{ ...link, onClick: handleLinkClick }} key={index} />
                ))}
            </div>
        </div>
    );
}

export default Navbar;
