import { useState, useEffect } from 'react';

import LabCard from './LabCard';

import { getLabs } from '../../api';

import { labMock } from '../../assets/images';
import './LabList.styles.scss';

const mock = {
    cyber: {
        name: " ",
        previewText: "  ,     ,     .",
        link: "/labs",
        preview: labMock,
    },
    cv: {
        name: "  ",
        previewText: "      ,    .",
        link: "/labs",
        preview: labMock,
    },
    st: {
        name: " SmartTech",
        previewText: "         .",
        link: "/labs",
        preview: labMock,
    },
    vr: {
        name: " VR  AR",
        previewText: "      , 3D-.",
        link: "/labs",
        preview: labMock,
    },
    mobile: {
        name: "  ",
        previewText: "    ,      .",
        link: "/labs",
        preview: labMock,
    },
    print: {
        name: " 3D-  ",
        previewText: " 3D-,   3D-     .",
        link: "/labs",
        preview: labMock,
    },
};

function LabList() {
    const [labList, setLabList] = useState(mock);

    useEffect(() => {
        const loadData = async () => {
            const newLabList = await getLabs();
            setLabList(newLabList);
        };
        loadData();
    }, []);

    return (
        <div className="row lab-list">
            {Object.values(labList).map(lab => <LabCard key={lab.id} lab={lab} />)}
        </div>
    );
}

export default LabList;